import React, {useRef, useEffect, useCallback} from "react";
import Quill from "quill";
import "quill/dist/quill.bubble.css";
import styled from "styled-components";
import palette from "../../lib/styles/palette";
import Responsive from "../common/Responsive";

const StyledEditor = styled(Responsive)`
    padding: 5rem 0;
`;

const TitleInput = styled.input`
    font-size: 3rem;
    outline: none;
    padding-bottom: 0.5rem;
    border: none;
    border-bottom: 1px solid ${palette.gray[4]};
    margin-bottom: 2rem;
    width: 100%;
`;

const LevelTimeBlock = styled.div`
    p {
        margin: 0;
        margin-bottom: 0.5rem;
        font-size: 1.25rem;
        font-weight: bold;
    }
`;

const QuillWrapper = styled.div`
    /*      padding  */
    .qu-editor {
        padding: 0;
        min-height: 320px;
        font-size: 1.125rem;
        line-height: 1.5;
    }
    .ql-editor.ql-blank:before {
        left: 0px;
    }
`;

const Editor = ({title, body, level, time, onChangeField}) => {
    const quillElement = useRef(null); // Quill  DivElement 
    const quillIstance = useRef(null); // Quill  
    const mounted = useRef(false); //        

    useEffect(() => {
        quillIstance.current = new Quill(quillElement.current, {
            theme: 'bubble',
            placeholder: ' !',
            modules: {
                // https://quilljs.com/docs/modules/toolbar/ 
                toolbar: [
                    [{header: '1'}, {header: '2'}],
                    ['bold', 'italic', 'underline', 'strike'],
                    [{list: 'ordered'}, {list: 'bullet'}],
                    ['blockquote', 'code-block', 'link', 'image'],
                ],
            },
        });

        // quill text-change   
        // : https://quilljs.com/docs/api/#event
        const quill = quillIstance.current;
        quill.on('text-change', (delta, oldDelta, source) => {
            if (source === 'user') {
                onChangeField({key: 'body', value: quill.root.innerHTML});
            }
        })
    }, [onChangeField]);

    //      body  .
    useEffect(() => {
        if (mounted.current) return;
        mounted.current = true;
        quillIstance.current.root.innerHTML = body;
    }, [body]);

    const onChangeTitle = useCallback(e => {
       onChangeField({key: 'title', value: e.target.value});
    }, [onChangeField]);

    return (
        <StyledEditor>
            <TitleInput
                value={title}
                onChange={onChangeTitle}
                placeholder={" !"}
            />
            <LevelTimeBlock>
                <p>
                    : {level}
                </p>
                <p>
                     : {time}
                </p>
            </LevelTimeBlock>
            <QuillWrapper>
                <div ref={quillElement}/>
            </QuillWrapper>
        </StyledEditor>
    )
};

export default React.memo(Editor);
