import React, {useCallback, useEffect, useState} from "react";
import {useDispatch, useSelector, shallowEqual} from "react-redux";
import {withRouter} from 'react-router-dom';
import {changeField, enterForm, leaveForm, login} from "../../modules/auth";
import AuthForm from "../../components/auth/AuthForm";
import {check} from "../../modules/user";

const LoginForm = ({history}) => {
    const [error, setError] = useState(null);
    const dispatch = useDispatch();
    const {form, initialize, auth, authError, user} = useSelector(({auth, user}) => ({
        form: auth.login,
        initialize: auth.initialize,
        auth: auth.auth,
        authError: auth.authError,
        user: user.user,
    }), shallowEqual);

    //    
    const onChange = useCallback(e => {
        const {name, value} = e.target;
        dispatch(
            changeField({
                form: 'login',
                key: name,
                value
            })
        )
    }, [dispatch]);

    //    
    const onSubmit = useCallback(e => {
        e.preventDefault();
        const {username, password} = form;
        dispatch(login({username, password}));
    }, [dispatch, form]);

    //        form 
    useEffect(() => {
        dispatch(enterForm('login'));

        return() => {
            dispatch(leaveForm('login'));
        }
    }, [dispatch]);

    //  / 
    useEffect(() => {
        if (initialize === 'login') {
            if (authError) {
                if (authError.response.status === 401) {
                    setError('    ');
                    return;
                }
                setError(' ');
                return;
            }
            if (auth) {
                dispatch(check());
            }
        }
    }, [initialize, auth, authError, dispatch]);

    // user    
    useEffect(() => {
        if (user) {
            history.push('/');
            try {
                localStorage.setItem('user', JSON.stringify(user));
            } catch (e) {
                console.log('localStorage is not working...');
            }
        }

    }, [history, user]);

    return (
        <AuthForm
            type={"login"}
            form={form}
            error={error}
            onChange={onChange}
            onSubmit={onSubmit}
        />
    )
};

export default withRouter(LoginForm);
