import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {withRouter} from 'react-router-dom';
import {changeField, enterForm, leaveForm, register} from "../../modules/auth";
import AuthForm from "../../components/auth/AuthForm";
import {check} from "../../modules/user";

const RegisterForm = ({history}) => {
    const [error, setError] = useState(null);
    const dispatch = useDispatch();
    const {form, initialize, auth, authError, user} = useSelector(({auth, user}) => ({
        form: auth.register,
        initialize: auth.initialize,
        auth: auth.auth,
        authError: auth.authError,
        user: user.user,
    }));

    //    
    const onChange = e => {
        const {name, value} = e.target;
        dispatch(
            changeField({
                form: 'register',
                key: name,
                value
            })
        )
    };

    //    
    const onSubmit = e => {
        e.preventDefault();
        const {username, password, passwordConfirm} = form;
        if ([username, password, passwordConfirm].includes('')) {
            setError('   ');
            return;
        }
        if (password !== passwordConfirm) {
            setError('  ');
            dispatch(changeField({form: 'register', key: 'password', value: ''}));
            dispatch(changeField({form: 'register', key: 'passwordConfirm', value: ''}));
            return;
        }
        dispatch(register({username, password}));
    };

    //     form 
    useEffect(() => {
        dispatch(enterForm('register'));

        return() => {
            dispatch(leaveForm('register'));
        }
    }, [dispatch]);

    //  / 
    useEffect(() => {
        if (initialize === 'register') {
            if (authError) {
                if (authError.response.status === 409) {
                    setError('  ');
                    return;
                }
                setError(' ');
                return;
            }
            if (auth) {
                dispatch(check());
            }
        }
    }, [initialize, auth, authError, dispatch]);

    // user    
    useEffect(() => {
        if (user) {
            history.push('/');
            try {
                localStorage.setItem('user', JSON.stringify(user));
            } catch (e) {
                console.log('localStorage is not working...');
            }
        }

    }, [history, user]);

    return (
        <AuthForm
            type={"register"}
            form={form}
            error={error}
            onChange={onChange}
            onSubmit={onSubmit}
        />
    )
};

export default withRouter(RegisterForm);
