import React from "react";
import Button from "../common/Button";
import "./GameStart.scss"

const GameStart = ({onStart, onLevelChange}) => {

    return (
        <div id={"start-root"}>
            <div id={"start-select-div"}>
                <label>
                    <h3> </h3>
                </label>
                <select
                    name={"level"}
                    onChange={onLevelChange}
                >
                    <option value={""}></option>
                    <option value={""}></option>
                    <option value={""}></option>
                </select>
            </div>
            <div id={"start-btn-div"}>
                <Button onClick={onStart}></Button>
            </div>
        </div>
    );
};

export default React.memo(GameStart);
